.add-icon-container {
  display: flex;
  flex-direction: column;
  width: 100px;
  height: 100px;
  background-color: rgba(62, 0, 158);
  z-index: 200;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  border-radius: 50%;
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  margin-bottom: 5%;
  margin-right: 5%;
  position: fixed;
  box-shadow: 1px 4px 30px 1px #888888;
  cursor: pointer;

  .add-icon {
    justify-content: center;
    color: white;
    align-items: center;
    font-size: 75px;
    margin-bottom: auto;
    position: fixed;
    margin-bottom: 15px;
  }

}

@media only screen and (max-width: 450px) {
  .add-icon-container {
    height: 50px;
    position: fixed;
    width: 50px;
    z-index: 20000000000;


    .add-icon {
      padding-top: 5px;
      padding-right: 2px;
      font-size: 37.5px;
    }
  }
}
